<template>
  <div class="request-card p-0 py-3">
    <div class="card_header d-flex align-items-center px-4 justify-content-between">
      <div class="avatar_name d-flex align-items-center">
        <div :class="['avatar', dir === 'ltr' ? 'mr-1' : 'ml-1']">
          <img width="50px" height="50px" src="@/assets/images/home/avatar.png" alt="Avatar">
        </div>
        <div class="name_reqPostedDate">
          <h2 class="text-body mb-0">{{ item.from_user.full_name }}</h2>
          <p class="mb-0">Offered {{ item.created_at }}</p>
        </div>
      </div>
      <div class="req_status rounded-pill">
        {{ item.status }}
      </div>
    </div>
    <hr class="my-2">
    <div class="card-heading px-4">
      <div class="location d-flex">
        <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/MapPoint.svg" alt="Avatar">
        <h4 class="text-body font-weight-bold"> {{ getFullLocation }}</h4>
      </div>
      <div class="card-budget">
        <p class="font-size-normal mb-0 font-weight-bold">
          <span>{{ item.budget }} </span>
          <span class="font-size-small">{{ $t('common.egp') }}</span>
        </p>
        <span class="font-size-smaller">
          Per {{ formating_price_type(item.term[0].toLowerCase()) }}
        </span>
      </div>
    </div>
    <div class="amenities_required d-flex align-items-center mb-2 px-4">
      <div class="amenity bed d-flex align-items-center" v-if="item.num_of_bed_rooms">
        <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/Bed.svg" alt="Beds">
        <p>{{ item.num_of_bed_rooms }} Bedrooms</p>
      </div>
      <div class="amenity bath d-flex align-items-center" v-if="item.num_of_bath_rooms">
        <img width="16px" height="16px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/Bathtub.svg" alt="Bathrooms">
        <p>{{ item.num_of_bath_rooms }} Bath</p>
      </div>
    </div>
    <div class="property_types mb-1 px-4" v-if="item.property_type">
      <p class="text-body"> #{{ getPropertyTypes }}</p>
    </div>
    <div class="d-flex mt-3 justify-content-start px-4">
      <div>
        <img width="14px" height="14px" :class="dir === 'ltr' ? 'mr-1' : 'ml-1'" src="@/assets/images/home/calender.svg" alt="Calender">
        <span class="font-size-small light-gray font-weight-bold"
          >{{ item.duration + $t('common.from') }}
        </span>
        <span class="font-size-small light-gray font-weight-bold">
          {{ $dateFns.format(new Date(item.move_in_date), 'dd MMM yy') }}</span
        >
      </div>
    </div>
    <hr />
    <div class="card-offers light-gray d-flex justify-content-between align-items-center px-4">
      <div class="recievedOffers">
        <span class="offers-number"> {{ item.offers_count }} </span>
        <span class="mx-2">{{ $t('offer.received_offers') }}</span>
      </div>
      <div class="waitingResponse">
        <span class="waitingResponse-number"> {{ item.offers_count }} </span>
        <span class="mx-2">Waiting your response</span>
      </div>
    </div>
    <div class="availability_edit mt-3 d-flex justify-content-between px-4">
      <div class="custom-control custom-switch" @click.stop>
        <input type="checkbox" class="custom-control-input" v-model="isAvailable" id="customSwitch1">
        <label class="custom-control-label" for="customSwitch1">{{ isAvailable ? 'Available' : 'Not Available' }}</label>
      </div>
      <div class="edit" @click.prevent="() => $bvModal.show(`edit_del-${item.id}`)">
        <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/NotePencil.svg" alt="Edit Request">
        Edit
      </div>
    </div>
    <b-modal :id="`edit_del-${item.id}`" centered title="BootstrapVue" hide-footer hide-header>
      <b-row>
        <b-col cols="12">
          <button class="edit_btn border-0 w-100 py-2 mb-3">
            <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/edit.svg" alt="Edit Request">
            Edit Request
          </button>
        </b-col>
        <b-col cols="12">
          <button class="delete_btn border-0 w-100 py-2">
            <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/trash.svg" alt="Delete Request">
            Delete Request
          </button>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  data(){
    return {
      isAvailable: true,
    }
  },
  computed: {
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
    getFullLocation(){
      let locations = Object.values(this.item.locations);
      var fullLocation = '';
      for(let location in locations.reverse()){
        fullLocation += this.dir === 'ltr' ? `${locations[location].en}, ` : `${locations[location].ar}, `; 
      }
      return fullLocation;
    },
  },
  methods: {
    formating_price_type(item){
      switch (item){
        case 'dy':
          return this.$t('form.postingRequest.stayingDetails.days')
        case 'my':
          return this.$t('form.postingRequest.stayingDetails.months')
        case 'yy':
          return this.$t('form.postingRequest.stayingDetails.years')
        case 'hr':
          return this.$t('form.postingRequest.stayingDetails.hours')
        case 'wy':
          return this.$t('form.postingRequest.stayingDetails.weeks')
        default:
          return false  
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.edit_btn {
  background-color: rgba(0, 120, 255, 0.2);
  color: rgba(0, 120, 255);
}
.delete_btn {
  background-color: rgba(255, 82, 82, 0.1);
  color: #FF5252;
}
.request-card {
  background: white;
  border: 1px solid #e3e5e5;
  border-radius: 16px;
  padding: 24px;
  margin: 16px 0;
  width: 100%;
  .card_header {
    .avatar_name {
      .name_reqPostedDate {
        & > p {
          font-size: 14px !important;
          color: #5A5A5A !important;
        }
      }
    }
    .req_status {
      background-color: rgba(255, 192, 37, 0.1);
      color: #FFC025 !important;
      font-weight: bold;
    }
  }

  .card-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .location {
      h4 {
        font-size: 14px !important;
        font-weight: 600 !important;
      }
    }

    .card-budget {
      text-align: center;

      p {
        color: #0078ff;
      }
      & > span {
        color: #3BB44A !important;
      }
    }
  }

  .amenities_required {
    gap: 20px;
    .amenity {
      gap: 5px;
      p {
        font-size: 12px !important;
        color: #000 !important;
      }
    }
  }

  .property_types {
    p {
      font-weight: 500;
      font-size: 12px !important;
    }
  }

  .card-offers {
    font-weight: bold;
    .recievedOffers{
      .offers-number {
        padding: 8px;
        background-color: #ff5252;
        color: white;
        border-radius: 50%;
        width: 25px;
        display: inline-block;
        height: 25px;
        line-height: 10px;
      }
    }
    .waitingResponse {
      span:last-of-type {
        font-size: 11px;
        color: #FFB500 !important;
      }
    }

  }
  .availability_edit {
    color: #000 !important;
    .custom-control-input:checked ~ .custom-control-label::before {
      background-color: #3BB44A;
      border-color: #3BB44A;
    }
  }
}
</style>
