<template>
  <div>
    <div class="tabs-header-container">
      <div class="tabs-header">
        <div class="header-container">
          <div class="header">
            <svg-icon name="PaperPlaneRight" width="34" height="34" />
            <h1 class="font-weight-semi">{{ $t('header.my_requests') }}</h1>
            <p>
              {{ $t('home.my_dashboard_details') }}
            </p>
          </div>
          <b-overlay rounded="sm" :opacity="1">
            <div class="rent-tabs">
              <b-tabs class="tabs-container" align="center" justified>
                <b-tab :title="dir === 'rtl' ? 'الطلبات المرسلة' : 'Posted requests'"
                  :active="requestType === 'public'" v-on:click="requestType = 'public'"></b-tab>
                <b-tab :title="dir === 'rtl' ? 'العروض المباشرة' : 'Direct offers'" :active="requestType === 'private'"
                  v-on:click="requestType = 'private'"></b-tab>
              </b-tabs>
            </div>
          </b-overlay>
        </div>
      </div>
      <b-overlay :show="sentOffersPropertiesLoading" rounded="sm" :opacity="1">
        <div v-if="
          !sentOffersPropertiesLoading && sentOffersProperties.length === 0
        ">
          <p class="text-center mt-2">{{ $t('offer.noRequests') }}</p>
        </div>
        <div v-if="
          !sentOffersPropertiesLoading &&
          sentOffersProperties.length > 0 &&
          sentOffersPropertiesMeta.per_page
        ">
          <b-container>
            <b-row>
              <b-col xl="4" lg="6" cols="12" v-for="property in sentOffersProperties" :key="property.id">
                <NuxtLink :to="localePath(`/dashboard/requests/${property.id}/offers`)">
                  <RequestCard_2 :item="property" />
                </NuxtLink>
              </b-col>
            </b-row>
          </b-container>

          <div class="mt-3">
            <Pagination :meta="sentOffersPropertiesMeta" />
          </div>
        </div>
      </b-overlay>
    </div>
  </div>
</template>

<script>
import RequestCard_2 from '@/components/dashboard/RequestCard_2';

export default {
  name: 'MyRequestsPage',
  components: {
    RequestCard_2,
  },
  data() {
    return {
      requestType: 'public',
      sentOffersProperties: [],
      sentOffersPropertiesMeta: {},
      sentOffersPropertiesLoading: true,
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "My Requests" : "طلباتى"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "My Requests" : "طلباتى",
        }
      ]
    }
  },
  methods: {
    async getSentOffersProperties() {
      this.sentOffersPropertiesLoading = true;

      const query = Object.keys(this.$route.query)
        .map((key) => `${key}=${this.$route.query[key]}`)
        .join('&');

      const url = `/user/me/sent-requests?type=${this.requestType}${query && `&${query}`
        }`;

      const { Data } = await this.$axios.$get(url);

      this.sentOffersProperties = Data.collection;
      this.sentOffersPropertiesMeta = Data.pagination;
      this.sentOffersPropertiesLoading = false;
    },
  },
  computed: {
    appEnv() {
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  watch: {
    requestType() {
      this.getSentOffersProperties();
    },
    '$route.query': '$fetch',
  },
  async fetch() {
    await this.getSentOffersProperties();
  },
};
</script>

<style lang="scss">
.tabs-header-container {
  background-color: #f7f9fa;
  position: relative;
  top: -25px;
  min-height: 100vh;

  @media screen and (max-width: 992px) {
    top: -45px;
  }

  .tabs-header {
    background-color: #fff;
    margin: 0px -15px;
    padding: 20px 25px 0px;
    box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);
    -webkit-box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);
    -moz-box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.26);

    .header-container {
      .header {
        padding: 25px;
        text-align: center;

        h1 {
          text-align: center;
          margin: 10px 0px;
          color: #000;
        }

        p {
          margin-bottom: 15px;
          text-align: center;
          color: #48535b;
        }
      }
    }
  }
}

.rent-tabs {
  padding: 15px;
  border-top: solid 1px #f2f4f5;

  .tabs-container {
    background-color: #f2f4f5;
    border-radius: 6px;
    padding: 4px;

    ul {
      border: none;

      .nav-item {
        a {
          color: #48535b;
          font-weight: bold;
          text-align: center;
        }

        .active {
          background-color: #0078ff !important;
          color: #fff;
          border: none;
          border-radius: 6px;
        }
      }
    }
  }
}

.offer-slot {
  margin-top: 10px;
}

.offer-status {
  border-radius: 3px;
  font-size: 12px;
  padding: 5px 8px;
  font-weight: bold;
  display: inline;
}

.action-btn {
  border: none;
  color: $mainColorLight;
  font-size: 12px;
  font-weight: bold;
  border-radius: 7px;
  background-color: rgba(0, 128, 253, 0.08);
  padding: 7px 15px;
}

.no-actions {
  font-size: 12px;
  color: $secondaryTextColor;
}

.review {
  font-size: 14px;

  .review-star {
    width: 15.6px;
    height: 14.9px;
  }

  .review-btn {
    color: $mainColorLight;
    border: none;
    border-radius: 7px;
    background-color: rgba(0, 128, 253, 0.08);
    font-size: 12px;
    font-weight: bold;
    padding: 7px 15px;
    display: flex;
    align-items: center;
  }
}

.rating-box {
  cursor: pointer;
  font-size: 12px;
  padding: 4px 5px;
  font-weight: 600;
}

.property-location {
  margin-bottom: 7px;
  font-size: 12px;
  color: $secondaryTextColor;
}

.actions {
  @media (max-width: 767px) {
    width: 100%;
  }

  .action {
    width: 33%;
    padding: 0 5px;

    @media (min-width: 667px) {
      width: 150px;
    }
  }
}

::v-deep() .nav-link {
  font-weight: 600;
  font-size: 16px;
  border: none;
  margin-bottom: 1px;

  &.active {
    color: $primary;
  }
}
</style>
